import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [parentInfo, setParentInfo] = useState({
    parentsUSCitizens: "",
    address: "",
    Relationship: "",
    parentwas: "",
    nameOfParent: "",
    dateOflegitimization: "",
    legalCustody: "",
  });
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const handleChange = (name, value) => {
    const values = parentInfo;
    values[name] = value;
    setParentInfo(values);
    onChangeField("e", "parentalInfo", parentInfo);
  };

  return (
    <Step
      heading={
        "If you are a permant resident child of U.S. citizen parent(s), please complete the following"
      }
    >
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={12} md={8} className="mt-4 align-items-center">
              <MDBRow center className="align-items-center">
                <MDBCol md={8} className=" align-content-center">
                  <div>
                    <h6 style={{ fontWeight: "bold" }}>
                      How many of your parents are U.S. citizens?
                    </h6>
                  </div>
                </MDBCol>
                <MDBCol md={4}>
                  <MDBFormInline className="justify-content-center">
                    <MDBInput
                      gap
                      onClick={(e) => handleChange("parentsUSCitizens", "one")}
                      checked={
                        parentInfo["parentsUSCitizens"] === "one" ? true : false
                      }
                      label="One"
                      type="radio"
                      id="radio1"
                      style={{ marginTop: "-0.3rem" }}
                      containerClass="mr-5"
                    />
                    <MDBInput
                      gap
                      onClick={() => handleChange("parentsUSCitizens", "Both")}
                      checked={
                        parentInfo["parentsUSCitizens"] === "Both"
                          ? true
                          : false
                      }
                      label="Both"
                      type="radio"
                      id="radio2"
                      style={{ marginTop: "-0.3rem" }}
                      containerClass="mr-5"
                    />
                  </MDBFormInline>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md={12}>
                  <MDBInput
                    color="pink"
                    label="Complete name of one U.S. citizen parent? "
                    group
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    type="text"
                    name="nameOfParent"
                    value={parentInfo["nameOfParent"]}
                    onChange={(e) => {
                      validate();
                      handleChange(e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0 mt-5"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md={12}>
                  <MDBInput
                    color="pink"
                    label="Address of the parent "
                    group
                    type="text"
                    name="address"
                    value={parentInfo["address"]}
                    onChange={(e) => {
                      validate();
                      handleChange(e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBRow>
                  <MDBCol sm={12} md={12} className="mt-3">
                    <div>
                      <h6 style={{ fontWeight: "bold" }}>
                        Relationship to you:
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol sm={12}>
                    <MDBFormInline>
                      <MDBCol>
                        <MDBInput
                          gap
                          onClick={(e) =>
                            handleChange("Relationship", "Natural Parent")
                          }
                          checked={
                            parentInfo["Relationship"] === "Natural Parent"
                              ? true
                              : false
                          }
                          label="Natural Parent"
                          type="radio"
                          id="radio1"
                          style={{
                            display: "flex",
                            marginLeft: "3.25rem",
                            marginTop: "-0.3rem",
                          }}
                          containerClass="mr-0"
                        />
                      </MDBCol>
                      <MDBCol>
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange("Relationship", "Adoptive Parent")
                          }
                          checked={
                            parentInfo["Relationship"] === "Adoptive Parent"
                              ? true
                              : false
                          }
                          label="Adoptive Parent"
                          type="radio"
                          id="radio2"
                          style={{
                            display: "flex",
                            marginLeft: "3.25rem",
                            marginTop: "-0.3rem",
                          }}
                          containerClass="mr-0  ml-0"
                        />
                      </MDBCol>

                      <MDBCol>
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange(
                              "Relationship",
                              "Parent of child legitimized after birth"
                            )
                          }
                          checked={
                            parentInfo["Relationship"] ===
                            "Parent of child legitimized after birth"
                              ? true
                              : false
                          }
                          label="legitimized after birth"
                          type="radio"
                          id="radio2"
                          style={{
                            display: "flex",
                            marginLeft: "1.25rem",
                            marginTop: "-0.3rem",
                          }}
                          containerClass="mr-0 ml-0"
                        />
                      </MDBCol>
                    </MDBFormInline>
                  </MDBCol>
                </MDBRow>
                <MDBRow className="align-items-center">
                  <MDBCol md={6} className="mt-3">
                    <div>
                      <h6 style={{ fontWeight: "bold" }}>
                        Does this parent have legal custody of you
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <MDBInput
                        gap
                        onClick={(e) => handleChange("legalCustody", "Yes")}
                        checked={
                          parentInfo["legalCustody"] === "Yes" ? true : false
                        }
                        label="Yes"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex", marginTop: "-0.3rem" }}
                        containerClass="mr-5"
                      />
                      <MDBInput
                        gap
                        onClick={(e) => handleChange("legalCustody", "no")}
                        checked={
                          parentInfo["legalCustody"] === "no" ? true : false
                        }
                        label="No"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex", marginTop: "-0.3rem" }}
                        containerClass="mr-5"
                      />
                    </MDBFormInline>
                  </MDBCol>
                </MDBRow>
                {parentInfo["Relationship"] ===
                  "Parent of child legitimized after birth" && (
                  <MDBCol md={12} className="mt-3">
                    <MDBInput
                      color="pink"
                      label="Date of legitimization "
                      group
                      type="date"
                      name="dateoLegalizationOrAdoption"
                      value={parentInfo.dateOflegitimization}
                      onChange={(e) => {
                        validate();
                        handleChange("dateOflegitimization", e.target.value);
                      }}
                      required
                      style={{ position: "static" }}
                      containerClass="mb-0 ml-0"
                    />
                  </MDBCol>
                )}
                {parentInfo["Relationship"] === "Adoptive Parent" && (
                  <MDBCol md={12} className="mt-3">
                    <MDBInput
                      color="pink"
                      label="Date of legitimization "
                      group
                      type="date"
                      name="dateoLegalizationOrAdoption"
                      value={parentInfo.dateOflegitimization}
                      onChange={(e) => {
                        validate();
                        handleChange("dateOflegitimization", e.target.value);
                      }}
                      required
                      containerClass="mb-0 "
                    />
                  </MDBCol>
                )}
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
