

// add conditionals --shubs

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBFormInline, MDBCard } from "mdbreact";
import Step from "./Step";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const [otherFactors, setOtherFactors] = useState({
    memberOfCommunistParty: "",
    beenInNaziAffiliations: "",
    participatedInProsecution: "",
    desertedArmedForces: "",
    appliedExemptionfromMilitaryService: "",
    failedToFileIncomeTaxReturn: "",
    hasDeportationProceedings: "",
    involvedInAlcoholPloygamy: "",
    falseTestimonyForBenefits: "",
    claimedToBeUSCitizen: "",
    mentallypatientorLegallyIncompetent: "",
    nobilityInForeignStateTies: "",
    hasBeenArrested: "",
    beenOnProbation: "",
    haveChildrenrequiringSupport: "",
    beenOnProbation: "",
    ReasonForProbation: "",
    ProbationDate: "",
    financialChildSupport: "",
    financialChildSupportObligations: "",
    beenInComplianceOfObligations: "",
    InComplianceWithObligationsNow:"",
  });


  const Questions = [
    {
      question:
        "Have you ever aided, supported, or been a member of a totalitarian or cummunist party? ",
      name: "memberOfCommunistParty",
    },
    {
      question: "Have you ever  been in any Nazi Affiliations?",
      name: "beenInNaziAffiliations",
    },
    {
      question:
        "Have you ever participated in the persecution of any person because of race, religion, national origin, or political opinion? ",
      name: "participatedInProsecution",
    },
    {
      question:
        "Have you ever deserted the Armed Forces of the U.S. or avoided being drafted? ",
      name: "desertedArmedForces",
    },
    {
      question: "Have you ever applied for exemption from military service? ",
      name: "appliedExemptionfromMilitaryService",
    },

    {
      question:
        "Have you ever been deported or had deportation proceedings against you? ",
      name: "hasDeportationProceedings",
    },

    {
      question: "Have you given false testimony for immigration benefits? ",
      name: "falseTestimonyForBenefits",
    },
    {
      question: "Have you ever claimed to be a U.S. citizen? ",
      name: "claimedToBeUSCitizen",
    },
    {
      question:
        "Have you ever declared legally incompetent or confirmed as a mental patient? ",
      name: "mentallypatientorLegallyIncompetent",
    },
    {
      question: "Do you hold nobility in any foreign state? Ties or lineage?",
      name: "nobilityInForeignStateTies",
    },
    {
      question:
        "Have you ever committed any crime or ever been arrested for violating any law (excluding traffic regulations)?",
      name: "hasBeenArrested",
    },
    {
      question: "Have you ever been on probation?",
      name: "beenOnProbation",
    },
    {
      question: "Do you have any children living with you, that require to be under financial child support obligations? ",
      name: "financialChildSupport",
    },
  ];

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter valid phone number" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
    
 
  const handleChange = (name,value) => {
    const values = otherFactors;
    console.log(values[1])
    values[name] = value;
    setOtherFactors(values);
    onChangeField("e","otherFactors",otherFactors)
  }
  
  return (
    <Step heading={" Other Factors "}>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <MDBInput
                        gap
                        onClick={() => handleChange(q.name, true)}
                        checked={otherFactors[q.name] === true ? true : false}
                        label="Yes"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                      />
                      <MDBInput
                        gap
                        onClick={() => handleChange(q.name, false)}
                        checked={otherFactors[q.name] === false ? true : false}
                        label="No"
                        type="radio"
                        id="radio2"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                      />
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

              {otherFactors.beenOnProbation && (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        Why have you been on probation ?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <MDBInput
                        gap
                        onChange={(e) => {
                          handleChange("ReasonForProbation", e.target.value);
                          validate();
                        }}
                        value={otherFactors.ReasonForProbation}
                        label="Reason"
                        type="text"
                        containerClass="mr-5"
                      />
                      <MDBInput
                        gap
                        onChange={(e) =>
                          handleChange("ProbationDate", e.target.value)
                        }
                        value={otherFactors.ProbationDate}
                        label="Probation ended"
                        type="date"
                        containerClass="mr-5"
                      />
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              )}

              {otherFactors.financialChildSupport && (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        What are the financial child support obligations ?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <MDBInput
                        gap
                        onChange={(e) => {
                          handleChange(
                            "financialChildSupportObligations",
                            e.target.value
                          );
                          validate();
                        }}
                        value={otherFactors.financialChildSupportObligations}
                        label="Obligations"
                        type="text"
                        containerClass="mr-5"
                      />
                    </MDBFormInline>
                  </MDBCol>
                  <h6>Have you always been in compliance with them ?</h6>
                  <MDBCol>
                    <MDBCol>
                      <MDBFormInline className="justify-content-center">
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange("beenInComplianceOfObligations", true)
                          }
                          checked={
                            otherFactors["beenInComplianceOfObligations"] ===
                            true
                              ? true
                              : false
                          }
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                        />
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange("beenInComplianceOfObligations", false)
                          }
                          checked={
                            otherFactors["beenInComplianceOfObligations"] ===
                            false
                              ? true
                              : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                        />
                      </MDBFormInline>
                    </MDBCol>
                  </MDBCol>
                  {otherFactors.beenInComplianceOfObligations && (
                    <>
                     <h6>Have you always been in compliance with them ?</h6>
                  <MDBCol>
                    <MDBCol>
                      <MDBFormInline className="justify-content-center">
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange("InComplianceWithObligationsNow", true)
                          }
                          checked={
                            otherFactors["InComplianceWithObligationsNow"] ===
                            true
                              ? true
                              : false
                          }
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                        />
                        <MDBInput
                          gap
                          onClick={() =>
                            handleChange("InComplianceWithObligationsNow", false)
                          }
                          checked={
                            otherFactors["InComplianceWithObligationsNow"] ===
                            false
                              ? true
                              : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                        />
                      </MDBFormInline>
                    </MDBCol>
                      </MDBCol>
                      </>
                  )}
                </MDBCard>
              )}

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
